<template>
  <div id="sign">
    <el-col
      :span="24"
      class="warp-main"
      v-loading="loading"
      element-loading-text="拼命加载中"
    >
      <!-- 登录 -->
      <div
        class="logIn"
        :class="{fold:isFold}"
        v-show="isShow1"
      >
        <!-- 退出 -->
        <div
          class="exit"
          @click="selectItem"
        >
          <img
            src="/static/PICS/exit.jpg"
            height="18"
            width="18"
          >
        </div>
        <h2 class="title">Sign in</h2>
        <input
          type="text"
          v-model="username"
          @click="recover($event)"
          @blur="validate($event,'\\S')"
          name="username"
          placeholder="请输入账号"
          class="logIn-input"
          id="username"
        >
        <div class="password-wrapper">
          <input
            type="password"
            @keyup="keyUpSubmit($event)"
            v-model="password2"
            @click="recover($event)"
            @blur="validate($event,'\\S')"
            name="password"
            placeholder="请输入密码"
            class="logIn-input password-input"
            id="password2"
            index='0'
          >
          <i
            class="fa-eye"
            @click="showpw(0,$event)"
          ></i>
        </div>
        <div class="remember">
          <input
            type="checkbox"
            name="checkbox"
            v-model="checked"
            id='remember'
          > 记住密码
        </div>
        <div class="register-text">
          <span
            class="text-button"
            @click="selectPage('register')"
          > 注册</span>
          <span> | </span>
          <span
            class="text-button"
            @click="selectPage('forPass')"
          > 忘记密码?</span>
        </div>
        <div class="register_more" @click="routerLink('/register')">会员注册，请点这里</div>
        <button
          type="submit"
          id="logIn-submit"
          @click="logIn"
        >登录</button>
      </div>
      <!-- 注册 -->
      <div
        class="Register"
        :class="{open:isOpen2}"
        v-show="isShow2"
      >
        <!-- 退出 -->
        <div
          class="exit"
          @click="selectItem"
        >
          <img
            src="/static/PICS/exit.jpg"
            height="18"
            width="18"
          >
        </div>
        <h2 class="title">Sign up</h2>
        <input
          type="text"
          v-model="reUsername"
          @click="recover($event)"
          @blur="validate($event,'\\S')"
          name="username"
          placeholder="请输入账号"
          class="re-input"
          id="re-username"
        >
        <div class="password-wrapper">
          <input
            type="password"
            v-model="rePassword2"
            @click="recover($event)"
            @blur="validate($event,'\\S')"
            name="password"
            placeholder="请输入密码"
            class="re-input password-input"
            id="re-password2"
            index='1'
          >
          <i
            class="fa-eye"
            @click="showpw(1,$event)"
          ></i>
        </div>
        <div class="password-wrapper">
          <input
            type="password"
            v-model="rePassword4"
            @click="recover($event)"
            @blur="validate($event,'\\S')"
            name="password"
            placeholder="请重复输入密码"
            class="re-input password-input"
            id="re-password4"
            index='2'
          >
          <i
            class="fa-eye"
            @click="showpw(2,$event)"
          ></i>
        </div>
        <div class="email-BIGBOX">
          <input
            type="text"
            v-model="reEmail"
            @click="recover($event)"
            @keyup="emailFill(0,$event)"
            @blur="validate($event,'\\w+@(qq|163|sina)+\\.com')"
            name="username"
            placeholder="请输入邮箱"
            class="re-input"
            id="re-email"
            autocomplete="off"
          >
          <div
            class="email-box"
            v-show="show[0]"
          >
            <span
              class="email-chance"
              @mousedown="fill(0,0,$event)"
            >{{fills[0]}}</span>
            <span
              class="email-chance"
              @mousedown="fill(0,1,$event)"
            >{{fills[1]}}</span>
            <span
              class="email-chance"
              @mousedown="fill(0,2,$event)"
            >{{fills[2]}}</span>
          </div>
        </div>
        <input
          type="text"
          @keyup="keyUpSubmit($event)"
          v-model="reCode"
          name="username"
          placeholder="验证码"
          class="re-input1"
          id="re-code"
        >
        <button
          type="submit"
          id="re-submit"
          ref="btn1"
          @click="register"
        >获取</button>
        <span
          class="re-text-button"
          @click="selectPage('re1')"
        >返回</span>

      </div>
      <!-- 忘记密码 -->
      <div
        class="ForPass"
        :class="{open:isOpen3}"
        v-show="isShow3"
      >
        <!-- 退出 -->
        <div
          class="exit"
          @click="selectItem"
        >
          <img
            src="/static/PICS/exit.jpg"
            height="18"
            width="18"
          >
        </div>
        <h2 class="title">忘记密码</h2>
        <input
          type="text"
          v-model="fpUsername"
          @click="recover($event)"
          @blur="validate($event,'\\S')"
          name="username"
          placeholder="请输入账号"
          class="logIn-input"
          id="fp-username"
        >
        <div class="email-BIGBOX">
          <input
            type="text"
            v-model="fpEmail"
            @click="recover($event)"
            @keyup="emailFill(1,$event)"
            @blur="validate($event,'\\w+@(qq|163|sina)+\\.com')"
            name="username"
            placeholder="请输入邮箱"
            class="logIn-input"
            id="fp-email"
            autocomplete="off"
          >
          <div
            class="email-box"
            v-show="show[1]"
          >
            <span
              class="email-chance"
              @mousedown="fill(1,3,$event)"
            >{{fills[3]}}</span>
            <span
              class="email-chance"
              @mousedown="fill(1,4,$event)"
            >{{fills[4]}}</span>
            <span
              class="email-chance"
              @mousedown="fill(1,5,$event)"
            >{{fills[5]}}</span>
          </div>
        </div>
        <span class="fp-text"> 请输入您的注册邮箱</span><br>
        <button
          type="submit"
          id="fp-submit"
          @click="forPass"
        >确认</button>
        <span
          class="fp-text-button"
          @click="selectPage('re2')"
        >返回</span><br>
        <span class="fp-text1"> Now you can check your email to retrieve your password.</span>
      </div>
      <span class="middle"></span>
    </el-col>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  name: "login",
  data() {
    return {
      //页面跳转
      isFold: false,
      isOpen2: false,
      isOpen3: false,
      isShow1: true,
      isShow2: false,
      isShow3: false,
      //显示密码
      isBlue: [false, false, false],
      //输入类input
      username: "",
      reUsername: "",
      fpUsername: "",
      password2: "",
      rePassword2: "",
      rePassword4: "",
      reCode: "",
      reEmail: "",
      fpEmail: "",
      //checkbox类input
      checked: false,
      //填充选项
      fills: ["", "", "", "", "", ""],
      show: [0, 0],
      loading: false
    };
  },
  methods: {
    //向父组件传值、组件重新初始化
    selectItem() {
      this.$emit("controlSign", false);
      this.password2 = "";
      this.rePassword2 = "";
      this.rePassword4 = "";
      this.reCode = "";
      this.reEmail = "";
      this.fpEmail = "";
    },

    //选择页面
    selectPage(item) {
      //跳转注册页面
      if (item == "register") {
        this.isFold = true;
        let that = this;
        setTimeout(function() {
          that.isShow1 = false;
          that.isShow2 = true;
        }, 300);
        setTimeout(function() {
          that.isOpen2 = true;
        }, 350);
      }

      //跳转找回密码页面
      if (item == "forPass") {
        this.isFold = true;
        let that = this;
        setTimeout(function() {
          that.isShow1 = false;
          that.isShow3 = true;
        }, 300);
        setTimeout(function() {
          that.isOpen3 = true;
        }, 350);
      }

      //返回页面
      if (item == "re1") {
        this.isOpen2 = false;
        let that = this;
        setTimeout(function() {
          that.isShow1 = true;
          that.isShow2 = false;
        }, 300);
        setTimeout(function() {
          that.isFold = false;
        }, 350);
      }
      if (item == "re2") {
        this.isOpen3 = false;
        let that = this;
        setTimeout(function() {
          that.isShow1 = true;
          that.isShow3 = false;
        }, 300);
        setTimeout(function() {
          that.isFold = false;
        }, 350);
      }
    },

    //键盘回车
    keyUpSubmit(e) {
      let key = window.event.keyCode;
      if (key === 13) {
        // console.log(e.target.id);
        if (e.target.id == "password2") {
          this.logIn(); //登录
        } else if (e.target.id == "re-code") {
          this.register(); //注册
        }
        // else if(e.target.id="fp-email"){
        //   this.forPass();//忘记密码
        // }
      }
    },

    //显示密码
    showpw(index, e) {
      e.stopPropagation();
      this.isBlue[index] = this.isBlue[index] == true ? false : true;
      let a = document.getElementsByClassName("fa-eye");
      a[index].style.color =
        this.isBlue[index] == true ? "#2e96ec" : "rgb(215, 215, 215)";
      let b = document.getElementsByClassName("password-input");
      let type = this.isBlue[index] == true ? "text" : "password";
      b[index].setAttribute("type", type);
      b[index].focus();
    },

    //验证格式
    validate(event, text) {
      let a = event.currentTarget;
      var re = new RegExp(text);
      if (!re.test(a.value)) {
        if (a.getAttribute("type") == "password")
          a.setAttribute("type", "text");
        a.style.color = "rgb(255, 0, 0)";
        a.value = "WRONG FORMAT!";
      }
      if (a.getAttribute("id") == "re-email") {
        if (this.show[0] == 1) this.show[0] = 0;
        this.$forceUpdate();
      }
      if (a.getAttribute("id") == "fp-email") {
        if (this.show[1] == 1) this.show[1] = 0;
        this.$forceUpdate();
      }
    },

    //恢复输入框样式
    recover(event) {
      let a = event.currentTarget;
      if (a.style.color == "rgb(255, 0, 0)") {
        if (a.getAttribute("name") == "password")
          a.setAttribute("type", "password");
        let b = document.getElementsByClassName("fa-eye");
        let i = 0;
        while (i < 3) {
          b[i].style.color = "rgb(215, 215, 215)";
          i++;
        }
        a.style.color = "rgb(0, 0, 0)";
        a.value = "";
      }
    },

    //邮箱自动填充
    //显示填充数据
    emailFill(index, a) {
      let key = window.event.keyCode;
      if (key === 13) {
        if ((a.target.id = "fp-email")) {
          this.forPass(); //忘记密码
        }
      }
      if (index == 0) {
        // console.log(this.reEmail);
        var val = this.reEmail;
        if (this.show[0] == 0) {
          this.show[0] = 1;
          var e = document.getElementsByClassName("email-box");
        }
        if (val.indexOf("@") != -1) {
          val = val.substr(0, this.reEmail.indexOf("@"));
        }
        this.fills[0] = val + "@qq.com";
        this.fills[1] = val + "@163.com";
        this.fills[2] = val + "@sina.com";
      }
      if (index == 1) {
        var val = this.fpEmail;
        if (this.show[1] == 0) {
          this.show[1] = 1;
        }
        if (val.indexOf("@") != -1) {
          val = val.substr(0, this.fpEmail.indexOf("@"));
        }
        this.fills[3] = val + "@qq.com";
        this.fills[4] = val + "@163.com";
        this.fills[5] = val + "@sina.com";
      }
      this.$forceUpdate();
    },

    //自动填充邮箱
    fill(index, i, e) {
      e.preventDefault();
      e.stopPropagation();
      var val = this.fills[i];
      // recover(obj);
      if (index == 0) {
        this.reEmail = val;
        document.getElementById("re-email").style.color = "rgb(0, 0, 0)";
      }
      if (index == 1) {
        this.fpEmail = val;
        document.getElementById("fp-email").style.color = "rgb(0, 0, 0)";
      }
      this.show[index] = 0;
      this.$forceUpdate();
    },
    //隐藏填充box
    // hideFill(i) {
    //   if (this.show[i] == 1) this.show[i] = 0;
    //   console.log(this.show[i]);
    // },

    //请求登录
    logIn() {
      let username = this.username;
      let password = this.password2;
      let that = this;
      this.loginBtn_click();
      let url = this.HOST + "/user/login";
      let params = Qs.stringify({
        username: username, //String用户名
        password: password //String密码
      });
      //启动loading动画
      that.loading = true;
      this.axios({
        method: "post",
        url: url,
        data: params
      })
        .then(response => {
          that.loading = false;
          if (response.data.result == "error") {
            let message = response.data.message;
            that.$message.error({
              showClose: true,
              message: message,
              duration: 2000
            });
          } else {
            // that.defined.setRank(response.data.message.rank);
            // localStorage.setItem("userRank", response.data.message.rank);
            that.$message.success({
              showClose: true,
              message: "登录成功",
              duration: 2000
            });
            that.selectItem();
            that.$emit("userSignIn", that.username);
            //后接其他操作
          }
        })
        .catch(response => {
          // console.log(response);
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "请求出现异常",
            duration: 2000
          });
        });
    },

    //请求注册
    register() {
      let username = this.reUsername;
      let password = this.rePassword2;
      let rePassword = this.rePassword4;
      let code = this.reCode;
      let email = this.reEmail;
      let btn = this.$refs.btn1;
      let that = this;
      if (btn.innerHTML == "注册") {
        //启动loading动画
        that.loading = true;
        //注册
        url = this.HOST + "/user/register";
        let params = Qs.stringify({
          checkCode: code
        });
        this.axios({
          method: "post",
          url: url,
          data: params
        })
          .then(response => {
            that.loading = false;
            if (response.data.result == "error") {
              let message = response.data.message;
              that.$message.error({
                showClose: true,
                message: message,
                duration: 2000
              });
              btn.innerHTML = "重新获取";
              that.reCode = "";
            } else {
              that.$message.success({
                showClose: true,
                message: "注册成功",
                duration: 2000
              });
              btn.innerHTML = "获取";
              that.reCode = "";
              //后接其他操作
              that.selectPage("re1");
            }
          })
          .catch(response => {
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "注册请求出现异常",
              duration: 2000
            });
            btn.innerHTML = "重新获取";
            that.reCode = "";
          });
      } else {
        //启动loading动画
        that.loading = true;
        //发送验证码
        var url = this.HOST + "/user/validate";
        let params = Qs.stringify({
          username: username, //String用户名
          password: password, //String密码
          // confirmPassword: rePassword, //String确认密码
          email: email //String邮箱
        });
        //验证验证码
        this.axios({
          method: "post",
          url: url,
          data: params
        })
          .then(response => {
            that.loading = false;
            if (response.data.result == "error") {
              let message = response.data.message;
              that.$message.error({
                showClose: true,
                message: message,
                duration: 2000
              });
              btn.innerHTML = "重新获取";
              that.reCode = "";
            } else {
              that.$message.success({
                showClose: true,
                message: "已发送验证码",
                duration: 2000
              });
              btn.innerHTML = "注册";
            }
          })
          .catch(response => {
            // console.log(response);
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "获取验证码出现异常",
              duration: 2000
            });
            btn.innerHTML = "重新获取";
            that.reCode = "";
          });
      }
    },

    //请求忘记密码
    forPass() {},

    //记住账号、密码
    loginBtn_click() {
      let strName = this.username;
      let strPass = this.password2;
      localStorage.setItem("keyName", strName);
      if (this.checked) {
        localStorage.setItem("keyPass", strPass);
      } else {
        localStorage.removeItem("keyPass");
      }
    },

    init() {
      //检测有无记住我
      // console.log(localStorage.getItem("keyName"));
      if (localStorage.getItem("keyName")) {
        this.username = localStorage.getItem("keyName");
      }
      if (localStorage.getItem("keyPass")) {
        this.checked = true;
        this.password2 = localStorage.getItem("keyPass");
      }
      // if (localStorage.getItem("userRank")){
      // let i = localStorage.getItem("userRank");
      // if(i===0||i){this.defined.setRank(i)}
      // }
    },

    routerLink(path) {
      this.selectItem();
      this.$router.push(path); //路由跳转
    },
  },
  mounted() {
    this.init();
  },
  activated() {
    this.init();
  }
};
</script>

