  cursor: pointer;
  line-height: 28px;
  padding-left: 3px;
}
.fold {
  transform: translateX(0) rotateY(90deg);
  opacity: 0;
}
.open {
  transform: translateX(0) rotateY(0deg);
  opacity: 1;
}
/*自适应*/
@media screen and (max-width: 1250px) {
  #logo {
    left: 100px;
  }
}
@media screen and (max-width: 1000px) {
  #logo {
    left: 30px;
  }
}
@media screen and (max-width: 850px) {
  #logo {
    transform: scale(0.7, 0.7);
    left: 0px;
  }
}
</style>



