<template>
  <div id="metEdit">
    <el-row class="warp">
      <el-col
        :span="24"
        class="warp-main"
        v-loading="loading"
        element-loading-text="拼命加载中"
      >
        <!--工具条-->
        <el-col
          :span="24"
          class="toolbar"
          style="padding-bottom: 0px;"
        >
          <el-form
            :inline="true"
            :model="filters"
          >
            <el-form-item>
              <el-input
                v-model="filters.content"
                placeholder="会议名"
                @keyup.enter.native="handleSearch"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="handleSearch"
              >查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="showAddDialog"
              >新增</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
        <el-table
          :data="items"
          highlight-current-row
          @selection-change="selsChange"
          style="width: 100%; margin-bottom:10px;"
        >
          <el-table-column
            type="selection"
            width="55"
          ></el-table-column>
          <el-table-column
            type="index"
            width="60"
          ></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form
                label-position="left"
                inline
                class="demo-table-expand"
              >
                <el-form-item label="[封面图片]">
                  <img :src="props.row.img" class="avatar2">
                </el-form-item>
                <el-form-item label="[内容导读]">
                  <div v-html="props.row.introduction"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="title"
            label="会议名称"
            sortable
          ></el-table-column>
          <el-table-column
            prop="host"
            label="举办方"
            width="220"
            sortable
          ></el-table-column>
          <el-table-column
            prop="address"
            label="地址"
            width="180"
            sortable
          ></el-table-column>
          <el-table-column
            prop="holdTime"
            label="举办日期"
            width="150"
            sortable
          ></el-table-column>
          <el-table-column
            label="操作"
            width="200"
          >
            <template slot-scope="scope">
              <el-button
                size="small"
                @click="showEditDialog(scope.$index,scope.row)"
              >编辑</el-button>
              <el-button
                type="danger"
                @click="delItem(scope.$index,scope.row)"
                size="small"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col
          :span="24"
          class="toolbar"
        >
          <el-button
            type="danger"
            @click="batchDeleteItems"
            :disabled="this.sels.length===0"
          >批量删除</el-button>
          <el-pagination
            layout="prev, pager, next"
            background
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;"
          >
          </el-pagination>
        </el-col>

        <!-- 编辑界面 -->
        <el-dialog
          title="编辑"
          :visible.sync="editFormVisible"
          :close-on-click-modal="true"
          top="4vh"
          :modal-append-to-body="false"
        >
          <el-form
            :model="editForm"
            label-width="100px"
            :rules="editFormRules"
            ref="editForm"
          >
            <el-form-item
              label="会议名"
              prop="title"
            >
              <el-input
                v-model="editForm.title"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="举办方"
              prop="host"
            >
              <el-input
                v-model="editForm.host"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="地址"
              prop="address"
            >
              <el-input
                v-model="editForm.address"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="日期"
              prop="holdTime"
            >
              <el-date-picker
                type="date"
                placeholder="举办日期"
                v-model="editForm.holdTime"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="editImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item
              label="简介内容"
              prop="introduction"
            >
              <el-input
                type="textarea"
                v-model="addForm.introduction"
                :rows="8"
              ></el-input>
            </el-form-item>
          </el-form>
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button
              type="primary"
              @click.native="editSubmit"
            >提交</el-button>
          </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog
          title="新增"
          :visible.sync="addFormVisible"
          :close-on-click-modal="true"
          top="4vh"
          :modal-append-to-body="false"
        >
          <el-form
            :model="addForm"
            label-width="80px"
            :rules="addFormRules"
            ref="addForm"
          >
            <el-form-item
              label="会议名"
              prop="title"
            >
              <el-input
                v-model="addForm.title"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="举办方"
              prop="host"
            >
              <el-input
                v-model="addForm.host"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="地址"
              prop="address"
            >
              <el-input
                v-model="addForm.address"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="日期"
              prop="holdTime"
            >
              <el-date-picker
                type="date"
                placeholder="举办日期"
                v-model="addForm.holdTime"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="addImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="addForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item
              label="简介内容"
              prop="introduction"
            >
              <el-input
                type="textarea"
                v-model="addForm.introduction"
                :rows="8"
              ></el-input>
            </el-form-item>
          </el-form>
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click.native="addFormVisible = false">取消</el-button>
            <el-button
              type="primary"
              @click.native="addSubmit"
              :loading="addLoading"
            >提交</el-button>
          </div>
        </el-dialog>

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from "vue2-editor";

export default {
  name: "metEdit",
  components: { VueEditor },
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      timeType: 0,
      //搜索框内容
      filters: {
        content: ""
      },
      //组件展示内容
      items: [
        {
          id: "",
          title: "标题信息",
          host: "举办方",
          holdTime: "举办日期",
          address: "地址信息",
          introduction: "简介区",
          img: null
        }
      ],
      total: 1, //会议总数
      pageSize: 12, //每页会议数
      page: 1, //当前所在页
      // limit: 10,
      loading: false, //loading  标志
      sels: [], //列表选中列
      //页面初始化相关数据
      //用于判断当前该发请求的categoryId
      initFormRules: [
        {
          name: "用户列表",
          //method: "",
          categoryId: ""
        },
        {
          name: "近期活动",
          //method: "",
          categoryId: ""
        },
        {
          name: "协会活动",
          //method: "",
          categoryId: ""
        },
        {
          name: "新闻热点",
          //method: "",
          categoryId: ""
        },
        {
          name: "其他",
          //method: "",
          categoryId: ""
        },
        {
          name: "行业动态",
          //method: "get",
          categoryId: 6
        },
        {
          name: "资讯热点",
          //method: "get",
          categoryId: 12
        },
        {
          name: "行业报告",
          //method: "",
          categoryId: ""
        },
        {
          name: "政策",
          //method: "get",
          categoryId: 7
        },
        {
          name: "通知公告",
          //method: "get",
          categoryId: 9
        },
        {
          name: "协会动态",
          //method: "get",
          categoryId: 10
        },
        {
          name: "协会会员",
          //method: "get",
          categoryId: ""
        },
        {
          name: "协会之窗",
          //method: "",
          categoryId: ""
        },
        {
          name: "教育培训",
          //method: "",
          categoryId: ""
        },
        {
          name: "企业新闻",
          //method: "get",
          categoryId: 5
        },
        {
          name: "品牌展厅",
          //method: "",
          categoryId: ""
        },
        {
          name: "会员动态",
          //method: "get",
          categoryId: 8
        },
        {
          name: "产品推荐",
          //method: "",
          categoryId: 2
        },
        {
          name: "产品新闻",
          //method: "",
          categoryId: 1
        },
        {
          name: "产品评测",
          //method: "",
          categoryId: 3
        },
        {
          name: "产品库",
          //method: "",
          categoryId: ""
        },
        {
          name: "技术研讨",
          //method: "",
          categoryId: ""
        },
        {
          name: "基础知识",
          //method: "",
          categoryId: 4
        },
        {
          name: "案例库",
          //method: "",
          categoryId: ""
        },
        {
          name: "专家库",
          //method: "get",
          categoryId: ""
        },
        {
          name: "专家智点",
          //method: "get",
          categoryId: 11
        },
        {
          name: "高校",
          //method: "get",
          categoryId: ""
        },
        // categoryId:会议：1  展会：2（不传默认1）
        // timeType       全部：1  未举办：2  已举办：3（不传默认全部）
        {
          name: "展会动态",
          //method: "",
          categoryId: "1",
          timeType: "1"
        },
        {
          name: "未来展会",
          //method: "",
          categoryId: "2",
          timeType: "2"
        },
        {
          name: "展会回顾",
          //method: "",
          categoryId: "2",
          timeType: "3"
        },
        {
          name: "会议动态",
          //method: "",
          categoryId: "1"
        },
        {
          name: "未来会议",
          //method: "",
          categoryId: "1",
          timeType: "2"
        },
        {
          name: "会议回顾",
          //method: "",
          categoryId: "1",
          timeType: "3"
        },
        {
          name: "广告1",
          //method: "",
          categoryId: ""
        },
        {
          name: "广告2",
          //method: "",
          categoryId: ""
        },
        {
          name: "广告3",
          //method: "",
          categoryId: ""
        }
      ],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"],
        ["clean"],
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ indent: "-1" }, { indent: "+1" }],
        ["blockquote", "code-block"],
        ["image", "link"]
      ],
      //编辑相关数据
      editCoverShow:false,//判断编辑封面显示
      editFormVisible: false, //编辑界面是否显示
      // 表单验证规则
      editFormRules: {
        title: [{ required: true, message: "请输入会议名", trigger: "blur" }],
        host: [{ required: true, message: "请输入举办方", trigger: "blur" }],
        address: [{ required: false, message: "请输入来源", trigger: "blur" }],
        introduction: [
          { required: false, message: "请输入内容", trigger: "blur" }
        ],
        holdTime: [{ required: true, message: "请填写日期", trigger: "blur" }]
      },
      //ref操作dom
      editForm: {
        id: 0,
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: {},
        img:""
      },
      //新增相关数据
      addCoverShow:false,//判断封面图片是否显示
      addFormVisible: false, //新增界面是否显示
      addLoading: false,
      // 表单验证规则
      addFormRules: {
        title: [{ required: true, message: "请输入会议名", trigger: "blur" }],
        host: [{ required: true, message: "请输入举办方", trigger: "blur" }],
        address: [{ required: false, message: "请输入来源", trigger: "blur" }],
        introduction: [
          { required: false, message: "请输入内容", trigger: "blur" }
        ],
        holdTime: [{ required: true, message: "请填写日期", trigger: "blur" }]
      },
      //ref操作dom
      addForm: {
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: "",
        img:"",
      }
    };
  },
  methods: {
    //换页
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //封面图片格式要求
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 /1024 < 10;
      // if (!isJPG) {
      //   this.$message.error('上传头像图片只能是 JPG 格式!');
      // }
      if (!isLt10M) {
        this.$message.error('上传头像图片大小不能超过 10MB!');
      }
      // return isJPG && isLt10M;
    },
    //新增
    //新增弹框
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        title: "",
        host: "",
        address: "",
        holdTime: "",
        introduction: ""
      };
    },
    //新增框上传封面图片操作
    addImgUpload(e){
      let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", e.file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            that.$message.success({
              showClose: true,
              message: "上传成功",
              duration: 2000
          });
          that.addCoverShow = true;
          that.addForm.img = response.data.message[0];
          }
          else{
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        })
    },
    //新增会议
    addSubmit() {
      let that = this;
      let para = Object.assign({}, that.addForm);
      if (para.title && para.holdTime && para.host) {
        that.$confirm("确认添加吗", "新增", { type: "warning" }).then(() => {
          let url = that.HOST + "/conference/addConference";
          let params = Qs.stringify({
            title: para.title,
            address: para.address,
            img:para.img,
            hold_time: para.holdTime,
            introduction:para.introduction,
            conference_type: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "新增成功",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    },

    //删除
    delItem(index, row) {
      let that = this;
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let postId = row.id;
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //批量选中
    selsChange: function(sels) {
      this.sels = sels;
    },
    //批量删除
    batchDeleteItems() {
      let that = this;
      this.$confirm("确认删除选中记录吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },

    //搜索
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //显示搜索会议（或初始化）
    search() {
      // console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + "/conference/findConference";
      that.categoryId = that.initFormRules[index].categoryId;
      that.timeType = that.initFormRules[index].timeType;
      let content = that.filters.content.trim();

      if (that.categoryId != "") {
        let data = {
          pageNum: that.page, //页数，默认为1
          categoryId: that.categoryId, //编号,筛选数据归属
          content: content,
          timeType: that.timeType
        };
        //     //启动loading动画
        that.loading = true;
        //     //发送get请求
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.list;
            that.items.forEach((item, index) => {
              that.items[index].holdTime = item.holdTime.slice(0, 10);
            });
            that.total = response.data.message.total;
            that.pageSize = response.data.message.pageSize;
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "数据异常",
          duration: 2000
        });
      }
    },

    //编辑
    //编辑弹框
    showEditDialog(index, row) {
      this.editFormVisible = true;
      if(row.img){that.editCoverShow=true;}
      this.editForm = Object.assign({}, row);
    },
    editImgUpload(e){
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST +"/common/uploadImg"
      let formData = new FormData();
      formData.append("imgs", e.file);
      that.axios({
        url: url,
        method: 'POST',
        headers: {'Content-Type': 'multipart/form-data'},
        data: formData
      })
      .then(response => {
        if(response.data.result == "success"){
          that.editCoverShow = true;
          that.editForm.img = response.data.message;
        }
        else{
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
        });
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "图片上传异常",
          duration: 2000
        });
      })      
    },
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //监听组件刷新内容
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss" scoped>
.el_b_form input{
  width: 50%!important;
}
.editor1 #quill-container{
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker{
  height: auto!important;
  margin-top:-6px; 
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
</style>


