<template>
  <div id="artEdit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.content" placeholder="文章名" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @keyup="keyUpSubmit($event)" @click="handleSearch">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog">新增</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[封面图片]">
                  <img :src="props.row.img" class="avatar2">
                </el-form-item>
                <el-form-item label="[内容导读]">
                  <div v-html="props.row.readGuide"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="文章名" sortable></el-table-column>
          <el-table-column prop="source" label="来源" width="100" sortable></el-table-column>
          <el-table-column prop="postDate" label="日期" width="150" sortable></el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button>
              <el-button type="danger" @click="delItem(scope.$index,scope.row)" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteItems" :disabled="this.sels.length===0">批量删除</el-button>
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!-- 编辑界面 -->
        <el-dialog title="编辑" :visible.sync="editFormVisible" top="4vh" :close-on-click-modal="true" :modal-append-to-body="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="文章名" prop="title">
              <el-input v-model="editForm.title" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="来源" prop="source">
              <el-input v-model="editForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="日期" prop="postDate">
              <el-date-picker type="date" placeholder="选择日期" v-model="editForm.postDate"></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="editImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="内容" prop="content">
              <vue-editor class="editor1" useCustomImageHandler @imageAdded="handleImageAdded" v-for="(item,index) of editFormContent" :editorToolbar="customToolbar" v-show="index+1==editCurrentPage" :key="index" v-model="item.content"></vue-editor>
              <!-- <el-input type="textarea" v-model="editForm.content[0].content" :rows="8"></el-input> -->
              <el-pagination style="margin-top;20px;" layout="prev, pager, next" :total="editFormContent.length" :page-size=1 :current-page.sync="editCurrentPage">
             </el-pagination>   
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="editSubmit">提交</el-button>
          </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog title="新增" :visible.sync="addFormVisible" top="4vh" :close-on-click-modal="true" :modal-append-to-body="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="文章名" prop="title">
              <el-input v-model="addForm.title" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="来源" prop="source">
              <el-input v-model="addForm.source" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="日期" prop="postDate">
              <el-date-picker type="date" placeholder="选择日期" v-model="addForm.postDate"></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload
                class="avatar-uploader"
                action="http://122.13.4.239/common/uploadImg"
                :http-request="addImgUpload"
                :auto-upload="true"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="addForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="内容" prop="content" :row="20">
              <vue-editor class="editor1" useCustomImageHandler @imageAdded="handleImageAdded" v-for="(item,index) of addFormContent" :editorToolbar="customToolbar" v-show="index+1==addCurrentPage" :key="index" v-model="item.content"></vue-editor>
              <!-- <el-input type="textarea" v-model="addForm..content[0].content" :rows="8"></el-input> -->
              <el-pagination style="margin-top;20px;" layout="prev, pager, next" :total=5 :page-size=1 :current-page.sync="addCurrentPage">
             </el-pagination>   
           </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
          </div>
        </el-dialog>

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from 'vue2-editor';

export default {
  name: "artEdit",
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      //搜索框内容
      filters: {
        content: ""
      },
      //组件展示内容
      items: [
        // {
        //   id: "",
        //   title: "标题信息",
        //   postDate: "上传时间",
        //   source: "来源信息",
        //   content: [
        //   {
        //     page: 1,
        //     content: "内容区"
        //   }
        // ]
        // }
      ],
      total: 1, //文章总数
      pageSize: 12, //每页文章数
      page: 1, //当前所在页
      // limit: 10,
      loading: false, //loading  标志
      sels: [], //列表选中列
      //页面初始化相关数据
      //用于判断当前该发请求的categoryId
      initFormRules: [
        {
          name: "用户列表",
          //method: "",
          categoryId: ""
        },
        {
          name: "近期活动",
          //method: "",
          categoryId: ""
        },
        {
          name: "协会活动",
          //method: "",
          categoryId: ""
        },
        {
          name: "新闻热点",
          //method: "",
          categoryId: ""
        },
        {
          name: "其他",
          //method: "",
          categoryId: ""
        },
        {
          name: "行业动态",
          //method: "get",
          categoryId: 6
        },
        {
          name: "资讯热点",
          //method: "get",
          categoryId: 12
        },
        {
          name: "行业报告",
          //method: "",
          categoryId: ""
        },
        {
          name: "政策",
          //method: "get",
          categoryId: 7
        },
        {
          name: "通知公告",
          //method: "get",
          categoryId: 9
        },
        {
          name: "协会动态",
          //method: "get",
          categoryId: 10
        },
        {
          name: "协会会员",
          //method: "get",
          categoryId: ""
        },
        {
          name: "协会之窗",
          //method: "",
          categoryId: ""
        },
        {
          name: "教育培训",
          //method: "",
          categoryId: ""
        },
        {
          name: "企业新闻",
          //method: "get",
          categoryId: 5
        },
        {
          name: "品牌展厅",
          //method: "",
          categoryId: ""
        },
        {
          name: "会员动态",
          //method: "get",
          categoryId: 8
        },
        {
          name: "产品推荐",
          //method: "",
          categoryId: 2
        },
        {
          name: "产品新闻",
          //method: "",
          categoryId: 1
        },
        {
          name: "产品评测",
          //method: "",
          categoryId: 3
        },
        {
          name: "产品库",
          //method: "",
          categoryId: ""
        },
        {
          name: "技术研讨",
          //method: "",
          categoryId: ""
        },
        {
          name: "基础知识",
          //method: "",
          categoryId: 4
        },
        {
          name: "案例库",
          //method: "",
          categoryId: ""
        },
        {
          name: "专家库",
          //method: "get",
          categoryId: ""
        },
        {
          name: "专家智点",
          //method: "get",
          categoryId: 11
        },
        {
          name: "高校",
          //method: "get",
          categoryId: ""
        },
        {
          name: "展会动态",
          //method: "",
          categoryId: ""
        },
        {
          name: "未来展会",
          //method: "",
          categoryId: ""
        },
        {
          name: "展会回顾",
          //method: "",
          categoryId: ""
        },
        {
          name: "会议动态",
          //method: "",
          categoryId: ""
        },
        {
          name: "未来会议",
          //method: "",
          categoryId: ""
        },
        {
          name: "会议回顾",
          //method: "",
          categoryId: ""
        },
        {
          name: "广告1",
          //method: "",
          categoryId: ""
        },
        {
          name: "广告2",
          //method: "",
          categoryId: ""
        },
        {
          name: "广告3",
          //method: "",
          categoryId: ""
        }
      ],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],  
        [{ 'header': [false,1, 2, 3, 4, 5, 6] }], 
        ['bold', 'italic', 'underline','strike'],['clean'],
        [{ 'align':""},{ 'align':"center"},{ 'align':"right"},{ 'align':"justify"}],
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        [{ 'indent': '-1'}, { 'indent': '+1' }],
        ['blockquote', 'code-block'],
        ['image','link', ], 
      ],
      //编辑相关数据
      editCurrentPage:1,//当前内容的页号
      editFormVisible: false, //编辑界面是否显示
      // 表单验证规则
      editFormRules: {
        title: [{ required: true, message: "请输入文章名", trigger: "blur" }],
        source: [{ required: false, message: "请输入来源", trigger: "blur" }],
        content: [{ required: true, message: "请输入内容", trigger: "blur" }],
        postDate: [{ required: false, message: "请填写日期", trigger: "blur" }]
      },
      //修改内容
      editFormContent:[
        {
          page: 1,
          content: ""
        },
        {
          page: 2,
          content: ""
        },
        {
          page: 3,
          content: ""
        },
        {
          page: 4,
          content: ""
        },
        {
          page: 5,
          content: ""
        }
      ],
      //判断修改封面图片是否显示
      editCoverShow:false,
      //ref操作dom
      editForm: {
        id: 0,
        title: "",
        source: "",
        postDate: "",
        img:"",
        content: [
          {
            page: 1,
            content: ""
          }
        ]
      },
      //新增相关数据
      addCurrentPage:1,//当前内容页号
      addFormVisible: false, //新增界面是否显示
      addLoading: false,
      // 表单验证规则
      addFormRules: {
        title: [{ required: true, message: "请输入文章名", trigger: "blur" }],
        source: [{ required: false, message: "请输入来源", trigger: "blur" }],
        content: [{ required: false, message: "请输入内容", trigger: "blur" }],
        postDate: [{ required: false, message: "请填写日期", trigger: "blur" }]
      },
      //新增内容
      addFormContent:[
        {
          page: 1,
          content: ""
        },
        {
          page: 2,
          content: ""
        },
        {
          page: 3,
          content: ""
        },
        {
          page: 4,
          content: ""
        },
        {
          page: 5,
          content: ""
        }
      ],
      //判断封面图片是否显示
      addCoverShow:false,
      //ref操作dom
      addForm: {
        title: "",
        source: "",
        postDate: "",
        img:"",
      }
    };
  },
  methods: {
    //换页
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //处理富文本框图片上传
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      console.log(file)
        let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            let url = response.data.message // Get url from response
            // console.log(url)
            Editor.insertEmbed(cursorLocation, 'image', url[0]);
            // resetUploader();
          }
          else{
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        })
    },
    //封面图片格式要求
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 /1024 < 10;
      // if (!isJPG) {
      //   this.$message.error('上传头像图片只能是 JPG 格式!');
      // }
      if (!isLt10M) {
        this.$message.error('上传头像图片大小不能超过 10MB!');
      }
      // return isJPG && isLt10M;
    },
//新增
    //新增弹框
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm =Object.assign(this.addForm, {
        postDate: "",
      })
      this.addFormContent=[
          {
            page: 1,
            content: ""
          },
          {
            page: 2,
            content: ""
          },
          {
            page: 3,
            content: ""
          },
          {
            page: 4,
            content: ""
          },
          {
            page: 5,
            content: ""
          }
        ]
    },
    //新增框上传封面图片操作
    addImgUpload(e){
      let that = this;
        // let params = Qs.stringify({
        //     imgs: file,
        //   });
        let url = that.HOST +"/common/uploadImg"
        let formData = new FormData();
        formData.append("imgs", e.file);
        that.axios({
          url: url,
          method: 'POST',
          headers: {'Content-Type': 'multipart/form-data'},
          data: formData
        })
        .then(response => {
          if(response.data.result == "success"){
            that.$message.success({
              showClose: true,
              message: "上传成功",
              duration: 2000
          });
          that.addCoverShow = true;
           that.addForm.img = response.data.message[0];
          }
          else{
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
          });
          }
        })
        .catch((err) => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        })
    },
    //新增文章
    addSubmit() {
      let that = this;
      let para = Object.assign({}, that.addForm);
      if (para.title && that.addFormContent[0].content) {
        that.$confirm("确认添加吗", "新增", { type: "warning" }).then(() => {
          let url = that.HOST + "/information/manager/addInformation";
          let params = Qs.stringify({
            title: para.title,
            source: para.source,
            img:para.img,
            // postDate: para.postDate,
            categoryId: that.categoryId,
            content:that.addFormContent
          },{arrayFormat: 'indices', allowDots: true});
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              that.addCoverShow = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "新增成功",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    },

//删除
    delItem(index, row) {
      let that = this;
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let postId = row.id;
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //批量选中
    selsChange: function(sels) {
      this.sels = sels;
    },
    //批量删除
    batchDeleteItems() {
      let that = this;
      this.$confirm("确认删除选中记录吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/information/manager/deleteInformation";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },

//搜索
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //键盘回车
    keyUpSubmit(e) {
      let key = window.event.keyCode;
      if (key === 13) {
        this.handleSearch()
      }
    },
    //显示搜索文章（或初始化）
    search() {
      // console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + "/common/search";
      that.categoryId = that.initFormRules[index].categoryId;
      let content = that.filters.content.trim();

      if (that.categoryId != "") {
        let data = {
          pageNum: that.page, //页数，默认为1
          categoryId: that.categoryId, //编号,筛选数据归属
          content: content,
          channel: 1
        };
        //启动loading动画
        that.loading = true;
        //发送get请求
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.results.list;
            that.items.forEach((item, index) => {
              that.items[index].postDate = item.postDate.slice(0, 10);
            });
            that.total = response.data.message.results.total;
            that.pageSize = response.data.message.results.pageSize;
            
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "数据异常",
          duration: 2000
        });
      }
    },

//编辑
    //编辑弹框
    showEditDialog(index, row) {
      let that = this;
      if(row.img){that.editCoverShow=true;}
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
      row.content.forEach((item,index) => {
      that.editFormContent[index].content = item.content.replace(/<\/div>/g, '</p>').replace(/<div/g, '<p'); //此处vue2-editor的v-model无法显示div
      });
    },
    //编辑封面图片
    editImgUpload(e){
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST +"/common/uploadImg"
      let formData = new FormData();
      formData.append("imgs", e.file);
      that.axios({
        url: url,
        method: 'POST',
        headers: {'Content-Type': 'multipart/form-data'},
        data: formData
      })
      .then(response => {
        if(response.data.result == "success"){
          that.editCoverShow = true;
          that.editForm.img = response.data.message;
        }
        else{
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
        });
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "图片上传异常",
          duration: 2000
        });
      })      
    },
    //编辑插入
    editSubmit(){
      let that = this;
      let para = Object.assign({}, this.editForm);
      if (para.title && para.id) {
        that.$confirm("确认更改吗", "编辑", { type: "warning" }).then(() => {
          let url = that.HOST + "/information/manager/updateInformation";
          let params = Qs.stringify({
            url: para.url,
            id: para.id,
            img:para.img,
            title: para.title,
            source: para.source,
            // postDate: para.postDate,
            // categoryId: that.categoryId
            content:that.editrFormContent}
            ,{arrayFormat: 'indices', allowDots: true})
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "编辑成功",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //监听组件刷新内容
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  },
  components:{VueEditor}
};
</script>
<style lang="scss">
.el_b_form input{
  width: 50%!important;
}
.editor1 #quill-container{
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker{
  height: auto!important;
  margin-top:-6px; 
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
.avatar2{
  @extend .avatar;
}
</style>


