<!--admin.vue-->
<template>
  <div id="admin">
    <div class="bg-img">
      <div class="bg-content">
        <m-header
          ref="c1"
          @controlSign="controlSign1"
          :username="username"
          :Rank="Rank"
          @userSignOut="userSignOut"
        ></m-header>
        <app
          :isShow="Rank==3"
        ></app>
        <sign
          id="sign"
          ref="sign"
          v-show="showSign"
          @controlSign="controlSign1"
          @userSignIn="userSignIn"
        ></sign>
        <div
          v-if="showSign"
          class="background-wall"
        ></div>
        <m-footer></m-footer>
        <!-- <canvas id='mycanvas'></canvas> -->
      </div>
    </div>
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";
import MFooter from "@/components/common/m-footer";
import Sign from "@/components/sign/sign";
import App from "@/components/admin/admin";

// var WIDTH = document.documentElement.clientWidth, HEIGHT = document.documentElement.clientHeight, POINT = 400;

export default {
  name: "admin",
  components: {
    MHeader,
    MFooter,
    Sign,
    App
  },
  data() {
    return {
      path: "",
      showSign: false,
      username: sessionStorage.username,
      Rank:0,//权限等级
    };
  },
  watch: {
    $route() {
      this.permissionJudge();    //判断权限等级
    }
  },
  methods: {
    controlSign1(result, type) {
      let that = this;
      //判断跳出页面
      if (type) {
        this.$refs.sign.selectPage(type);
        setTimeout(function() {
          //显示跳出页面
          that.showSign = result;
        }, 200);
      } else {
        this.showSign = result;
      }
    },
    userSignIn(username) {
      this.permissionJudge();
      sessionStorage.username = username;
      this.username = sessionStorage.username;
    },
    userSignOut() {
      this.permissionJudge();
      sessionStorage.username = "";
      this.username = sessionStorage.username;
    },
    //判断权限等级
    permissionJudge(){
      let that = this;
      that.axios
      .get(that.HOST + "/user/getPermission")
      .then(response => {
        if (response.data.result == "success") {
          that.Rank = response.data.message;
          localStorage.setItem("userRank",response.data.message);
        }
      })
      .catch(res => {
        that.$message.error({
          showClose: true,
          message: "获取登录状态失败",
          duration: 2000
        });
      });
    }
  },
  mounted: function() {
    // console.log(this.username);
    // 获取当前路径
    // let path = this.$route.path;
    let pathName = document.location.href;
    let index = pathName.substr(1).indexOf(".html");
    let path = pathName.substr(0, index + 1);
    // console.log(path);
    // 检索当前路径
    this.$refs.c1.checkRouterLocal(path);
  }, //确保刷新后页面初始化不出错
  created() {
    this.permissionJudge();    //判断权限等级
    // if (localStorage.getItem("userRank")) {
      // let i = localStorage.getItem("userRank");
      // if (i === 0 || i) {
      //   this.defined.setRank(i);
      // }
    // }
    // console.log(this.isAdmin, this.defined.isAdmin);
  },

};
</script>

<style lang="scss">
body {
  margin: 0;
  font-size: 16px;
  font-family: "微软雅黑", "Microsoft Yahei", Verdana, sans-serif;
  min-width: 1002px;
  // background: url(/static/PICS/ui-background.jpg) center top repeat rgb(255, 255, 255);
  // background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg) center top repeat rgb(255, 255, 255);
}
.bg-img {
  position: relative;
  // width: 100%;
  min-width: 1200px;
  height: 100%;
  background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg)
    center top repeat;
}
.bg-img::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom right, #26486e, #cc998f);
  opacity: 0.3;
  z-index: 1;
}
.bg-content {
  z-index: 2;
  position: relative;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
input:focus {
  outline: none;
}
a {
  text-decoration: none;
  color: inherit;
}
.background-wall {
  background-color: #000000;
  width: 100%;
  height: 100%;
  top: 0;
  position: fixed;
  clear: both;
  z-index: 5;
  opacity: 0.5;
}
</style>


